"use client";

import React, { useState, useEffect } from "react";
import { format, addDays, startOfWeek, addWeeks, startOfMonth, addMonths, isSameDay } from "date-fns";
import { ja } from "date-fns/locale";
import { Dialog, DialogContent, DialogTrigger } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";

// Only for client side visual, in real app fetch from API
import { getBookings } from "@/lib/db-local"; // Note: this imports node module in client.
// We should strictly separate. I will just create a client-side friendly mock or fetch wrapper.
// Since getBookings uses fs, it fails in client. We need a server action wrapper or API.

interface ScheduleViewProps {
    bookings: any[];
}

export const ScheduleView = ({ initialBookings = [] }: { initialBookings?: any[] }) => {
    const [viewMode, setViewMode] = useState<"month" | "week">("week");
    const [currentDate, setCurrentDate] = useState(new Date());
    const [isOpen, setIsOpen] = useState(false);

    // Simple Month View
    const renderMonth = () => {
        const start = startOfMonth(currentDate);
        const end = addMonths(start, 1);
        const days = [];
        let day = start; // Iterate until start of next month
        // Just show 35 days grid for simplicity
        const gridStart = startOfWeek(start);

        // Create 5 weeks
        for (let i = 0; i < 35; i++) {
            days.push(addDays(gridStart, i));
        }

        return (
            <div className="grid grid-cols-7 gap-1 text-center text-xs">
                {["日", "月", "火", "水", "木", "金", "土"].map(d => <div key={d} className="text-gray-400 py-1">{d}</div>)}
                {days.map((d, idx) => {
                    const isToday = isSameDay(d, new Date());
                    const dayBookings = initialBookings.filter(b => isSameDay(new Date(b.date), d));
                    const busyLevel = dayBookings.length;

                    return (
                        <div key={idx} className={`p-2 border border-white/5 ${isToday ? 'bg-cyan-900/50 text-cyan-200' : ''}`}>
                            <div className="opacity-50">{format(d, "d")}</div>
                            <div className="h-4 mt-1">
                                {busyLevel > 0 && (
                                    <div className="w-full h-full bg-red-500/50 rounded-xs text-[9px] flex items-center justify-center">
                                        {busyLevel}件
                                    </div>
                                )}
                            </div>
                        </div>
                    )
                })}
            </div>
        )
    };

    const renderWeek = () => {
        const start = startOfWeek(currentDate);
        const days = [];
        for (let i = 0; i < 7; i++) {
            days.push(addDays(start, i));
        }

        return (
            <div className="grid grid-cols-7 gap-1 text-center text-xs h-64">
                {days.map((d, idx) => {
                    const dayBookings = initialBookings.filter(b => isSameDay(new Date(b.date), d));
                    return (
                        <div key={idx} className="border border-white/10 flex flex-col">
                            <div className="p-1 border-b border-white/10 bg-white/5">
                                {format(d, "MM/dd", { locale: ja })}<br />
                                ({format(d, "EE", { locale: ja })})
                            </div>
                            <div className="flex-1 p-1 space-y-1 overflow-y-auto">
                                {dayBookings.map((b: any, i: number) => (
                                    <div key={i} className="bg-red-500/50 p-1 rounded text-[10px] truncate" title={`${b.startTime} - User: ${b.userId}`}>
                                        {b.startTime}〜
                                    </div>
                                ))}
                                {dayBookings.length === 0 && <span className="text-gray-600 block mt-4 text-[10px]">空き</span>}
                            </div>
                        </div>
                    )
                })}
            </div>
        )
    }

    return (
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild>
                <Button variant="outline" className="border-cyan-500/50 text-cyan-400 hover:bg-cyan-900/20 bg-black/50 backdrop-blur-md">
                    📅 空き状況を確認
                </Button>
            </DialogTrigger>
            <DialogContent className="bg-black/95 border-cyan-500/30 text-white max-w-3xl">
                <div className="flex justify-between items-center mb-4">
                    <h2 className="text-xl font-bold font-mono text-cyan-400">STUDIO SCHEDULE</h2>
                    <div className="flex gap-2">
                        <Button size="sm" variant={viewMode === 'month' ? 'default' : 'outline'} onClick={() => setViewMode('month')}>月表示</Button>
                        <Button size="sm" variant={viewMode === 'week' ? 'default' : 'outline'} onClick={() => setViewMode('week')}>週表示</Button>
                    </div>
                </div>

                <div className="flex justify-between items-center mb-4 px-2">
                    <Button size="sm" variant="ghost" onClick={() => setCurrentDate(curr => addWeeks(curr, -1))}>← 前回</Button>
                    <span className="font-bold">{format(currentDate, "yyyy年 M月", { locale: ja })}</span>
                    <Button size="sm" variant="ghost" onClick={() => setCurrentDate(curr => addWeeks(curr, 1))}>次回 →</Button>
                </div>

                {viewMode === 'month' ? renderMonth() : renderWeek()}

                <div className="mt-4 text-xs text-gray-500 text-right">
                    ※ 実際の予約は最新の状況と異なる場合があります。
                </div>
            </DialogContent>
        </Dialog>
    );
};
